import { SetErrors } from '../../../..'
import { ERROR_TYPE, REF_ORDER_ERROR_KEYS } from '../../../config/enums'

/**
 *        2,   2       .
 *       ,     .
 *            [1, 2, 3]  [1-5]  [1, 2, 3-5]  [1-3, 5]  [1]
 *
 * @param str string of reference matches by pattern
 * @param setErrors handler to set errors
 */
export const checkInsideReferenceSymbols = (str: string, setErrors: SetErrors): void => {
  const inside = str.slice(3, str.length - 2)
  const items = inside.split(',')

  if (items.length === 1) {
    if (items[0].trim() !== items[0] && !/^\d+|\d+-\d+$/.test(items[0])) {
      setErrors(ERROR_TYPE.referenceOrder, REF_ORDER_ERROR_KEYS.rule1, inside)
    }
  } else {
    for (let i = 1; i < items.length; i++) {
      const item = items[i]
      const isWrongSeparator = i === items.length - 1 && (item.endsWith(',') || item.endsWith(' '))
      const isCorrectNumbers = /^\s\d+|\s\d+-\d+$/.test(item)

      if (!isCorrectNumbers || isWrongSeparator) {
        setErrors(ERROR_TYPE.referenceOrder, REF_ORDER_ERROR_KEYS.rule1, inside)
      }
    }
  }
}
