import { SetErrors } from '../../../..'
import { ERROR_TYPE, REF_ORDER_ERROR_KEYS } from '../../../config/enums'

// todo:              
/**
 *     2   .
 * 1)     ,       rule3
 * 2)         rule2
 *
 * @param str string of reference matches by pattern
 * @param setErrors handler to set errors
 */
export const checkStarrSymbols = (str: string, setErrors: SetErrors): void => {
  const [firstSymbol, secondSymbol] = str
  if (firstSymbol === ',' || firstSymbol === '.' || firstSymbol === '!' || firstSymbol === '?' || firstSymbol === ' ') {
    setErrors(ERROR_TYPE.referenceOrder, REF_ORDER_ERROR_KEYS.rule3, str)
  }
  if (secondSymbol !== ' ') {
    setErrors(ERROR_TYPE.referenceOrder, REF_ORDER_ERROR_KEYS.rule2, str)
  }
}
